import React, { useState, useRef, useEffect } from 'react';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import { motion } from 'framer-motion';
import { X, ChevronLeft, ChevronRight, LogOut } from 'lucide-react';
import { gsap } from 'gsap';
import { cn } from '@/lib/utils';
import { useAuth } from '@/context/AuthContext';
import { usePermissions } from '@/context/PermissionContext';
import { useMobileMenu } from '@/context/MobileMenuContext';
import { ScrollArea } from '@/components/ui/scroll-area';
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from '@/components/ui/tooltip';
import { getIconByName } from './DynamicSidebarGenerator';
import { FireIcon as HeroFireIcon } from '@heroicons/react/24/solid';
import LogoutModal from '@/components/LogoutModal';


// Dynamic sidebar item interface
interface DynamicSidebarItemType {
  id: string;
  icon: React.ReactNode;
  label: string;
  href: string;
  pagePath?: string;
  requiredPageAction?: 'view' | 'add' | 'update' | 'delete';
  requireSuperAdmin?: boolean;
  isPublic?: boolean;
  category?: string;
  sortOrder?: number;
}

// Animated Fire Icon component
interface AnimatedFireIconProps {
  className?: string;
  isAnimated?: boolean;
}

const AnimatedFireIcon = React.forwardRef<HTMLDivElement, AnimatedFireIconProps>(
  ({ className, isAnimated = false }, ref) => (
    <motion.div
      ref={ref}
      animate={isAnimated ? {
        scale: [1, 1.1, 1],
        rotate: [0, 5, -5, 0],
      } : {}}
      transition={{
        duration: 2,
        repeat: isAnimated ? Infinity : 0,
        repeatType: "reverse",
      }}
      className={className}
    >
      <HeroFireIcon className="w-full h-full text-orange-500" />
    </motion.div>
  )
);

AnimatedFireIcon.displayName = 'AnimatedFireIcon';

// Animated Aura component for sidebar items
interface AnimatedAuraProps {
  isActive: boolean;
  colorScheme?: 'purple' | 'orange';
}

const AnimatedAura: React.FC<AnimatedAuraProps> = ({ isActive, colorScheme = 'purple' }) => {
  if (!isActive) return null;

  const colors = {
    purple: "bg-purple-500/20",
    orange: "bg-orange-500/20",
  };

  return (
    <div className="absolute inset-0 pointer-events-none overflow-hidden rounded-lg">
      <motion.div
        className={`absolute inset-0 ${colors[colorScheme]} blur-[2px]`}
        initial={{ opacity: 0 }}
        animate={{
          opacity: [0.3, 0.6, 0.3],
          scale: [0.95, 1.05, 0.95],
        }}
        transition={{
          duration: 2,
          repeat: Infinity,
          repeatType: "reverse",
          ease: "easeInOut",
        }}
      />
    </div>
  );
};

// Dynamic Sidebar Item component
interface DynamicSidebarItemProps {
  item: any;
  isCollapsed: boolean;
  isActive: boolean;
  onClick?: () => void;
}

const DynamicSidebarItem: React.FC<DynamicSidebarItemProps> = ({
  item,
  isCollapsed,
  isActive,
  onClick
}) => {
  const content = (
            <div
              className={cn(
                "flex items-center cursor-pointer rounded-md text-foreground transition-all duration-200 border border-transparent",
                "relative overflow-hidden",
                isCollapsed ? "p-3" : "px-3 py-3",
                !isCollapsed && "space-x-4",
                isActive && isCollapsed
                  ? "bg-primary/55 border-primary/30"
                  : isActive
                    ? "bg-primary/10 border-primary/20 font-semibold"
                    : "hover:bg-secondary/80 hover:border-secondary/40",
                item.className || ""
              )}
            >
              <AnimatedAura isActive={isActive} />

              <div className={cn(
                "flex-shrink-0 transition-all duration-200 relative z-10",
                isCollapsed ? "w-full flex justify-center items-center" : "w-6 h-6",
                isActive ? "text-primary" : "text-foreground"
              )}>
                <div className={cn(
                  "w-6 h-6 flex items-center justify-center",
                  isActive && isCollapsed && "scale-110"
                )}>
                  {item.icon}
                </div>
              </div>

              {!isCollapsed && (
                <span className={cn(
                  "text-sm transition-all duration-200 relative z-10",
                  isActive ? "font-semibold" : "font-medium"
                )}>
                  {item.label}
                </span>
              )}
            </div>
  );

  return (
    <TooltipProvider>
      <Tooltip>
        <TooltipTrigger asChild>
          {onClick ? (
            <button onClick={onClick} className={cn("w-full", item.className?.includes('w-full') ? 'block' : '')}>
              {content}
            </button>
          ) : (
            <Link to={item.href} className={cn("w-full", item.className?.includes('w-full') ? 'block' : '')}>
              {content}
            </Link>
          )}
        </TooltipTrigger>
        {isCollapsed && (
          <TooltipContent side="right" className="ml-2">
            <p>{item.label}</p>
          </TooltipContent>
        )}
      </Tooltip>
    </TooltipProvider>
  );
};

// Main Dynamic Sidebar component
interface DynamicSidebarProps {
  isCollapsed: boolean;
  onToggle: () => void;
}

const DynamicSidebar: React.FC<DynamicSidebarProps> = ({ isCollapsed, onToggle }) => {
  const navigate = useNavigate();
  const location = useLocation();
  const { logout } = useAuth();
  const permissionContext = usePermissions();
  const { isMobileMenuOpen, isMobileView, closeMobileMenu } = useMobileMenu();
  const [showLogoutModal, setShowLogoutModal] = useState(false);


  // Refs for animations
  const toggleButtonRef = useRef<HTMLButtonElement>(null);
  const logoRef = useRef<HTMLImageElement>(null);

  // Generate dynamic sidebar items from user's page permissions
  const generateDynamicSidebarItems = (): DynamicSidebarItemType[] => {
    const { pagePermissions, isSuperAdmin } = permissionContext;

    if (!pagePermissions || pagePermissions.length === 0) {
      return [];
    }

    return pagePermissions
      .filter(page => {
        
        // For super admin, show all pages
        if (isSuperAdmin) return true;

        // For regular users, only show pages they can view
        return page.can_view;
      })
      .map(page => ({
        
        id: page.page_path.replace('/', '') || 'root',
        icon: getIconByName(page.icon),
        label: page.page_name,
        href: page.page_path,
        pagePath: page.page_path,
        requiredPageAction: 'view' as const,
        category: page.page_category,
        sortOrder: page.sort_order || 0
      }))
      .sort((a, b) => (a.sortOrder || 0) - (b.sortOrder || 0));
  };

  // Get dynamic sidebar items
  const dynamicItems = generateDynamicSidebarItems();

  // Group items by category
  const groupedItems = dynamicItems.reduce((groups, item) => {
    const category = item.category || '';
    if (!groups[category]) {
      groups[category] = [];
    }
    groups[category].push(item);
    return groups;
  }, {} as Record<string, DynamicSidebarItemType[]>);

  // Function to check if a route is active
  const isRouteActive = (path: string): boolean => {
    if (path === '/' && location.pathname === '/') {
      return true;
    }
    if (path !== '/' && location.pathname.startsWith(path)) {
      return true;
    }
    return false;
  };

  // Handle mobile navigation
  const handleMobileNavigation = (path: string) => {
    navigate(path);
    closeMobileMenu();
  };

  // Handle logout
  const handleLogout = () => {
    setShowLogoutModal(true);
  };

  const handleLogoutConfirm = () => {
    setShowLogoutModal(false);
    logout();
    if (isMobileView) {
      closeMobileMenu();
    }
  };

  const handleLogoutCancel = () => {
    setShowLogoutModal(false);
  };

  // GSAP animations for toggle button and logo
  useEffect(() => {
    const toggleButton = toggleButtonRef.current;
    const logo = logoRef.current;

    if (toggleButton && logo && !isMobileView) {
      // Animate the logo size
      gsap.to(logo, {
        width: isCollapsed ? 16 : 32,
        duration: 0.3,
        ease: "power2.inOut",
      });

      // Animate the toggle button position
      gsap.to(toggleButton, {
        right: isCollapsed ? -16 : -32,
        duration: 0.3,
        ease: "power2.inOut",
      });
    }
  }, [isCollapsed, isMobileView]);

  // Sidebar styling
  const collapsedWidth = '80px';
  const expandedWidth = '280px';
  
  const sidebarClasses = cn(
    "fixed left-0 top-0 h-full bg-background border-r border-border z-50 transition-all duration-300 ease-in-out flex flex-col",
    isMobileView 
      ? `${isMobileMenuOpen ? 'translate-x-0' : '-translate-x-full'} w-80`
      : 'translate-x-0'
  );

  if (permissionContext.loading) {
    return (
      <div className={sidebarClasses} style={{ width: isCollapsed ? collapsedWidth : expandedWidth }}>
        <div className="flex items-center justify-center h-full">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary"></div>
        </div>
      </div>
    );
  }

  return (
    <>
    <div
      className={sidebarClasses}
      style={!isMobileView ? { width: isCollapsed ? collapsedWidth : expandedWidth } : undefined}
    >
      {/* Mobile close button */}
      {isMobileView && (
        <button
          onClick={closeMobileMenu}
          className="absolute top-4 right-4 p-1 rounded-full bg-secondary/80 hover:bg-secondary text-foreground"
          aria-label="Close menu"
        >
          <X className="h-5 w-5" />
        </button>
      )}

      {/* Logo */}
      <div className="flex items-center justify-center mb-8 pt-6">
        <AnimatedFireIcon
          ref={logoRef}
          className="w-8 h-8"
          isAnimated={location.pathname === "/"}
        />
        {(!isCollapsed || isMobileView) && (
          <h1 className="ml-4 text-lg font-bold font-mono tracking-widest">
            aicruitment
          </h1>
        )}
      </div>

      {/* Desktop toggle button - only show on desktop */}
      {!isMobileView && (
        <button
          ref={toggleButtonRef}
          onClick={onToggle}
          className="h-8 w-8 flex items-center justify-center rounded-full bg-gray-800 focus:outline-none text-white absolute top-1/2 -right-3 transform -translate-y-1/2 z-50 shadow-md hover:bg-gray-700 transition-colors"
          aria-label={isCollapsed ? "Expand sidebar" : "Collapse sidebar"}
        >
          {isCollapsed ? <ChevronRight className="h-4 w-4" /> : <ChevronLeft className="h-4 w-4" />}
        </button>
      )}

      {/* Navigation Items */}
      <ScrollArea className="flex-1 px-4">
        <nav className="flex flex-col space-y-2">
          {Object.keys(groupedItems).length === 0 ? (
            // Show message when no items are available
            <div className="flex flex-col items-center justify-center py-8 text-center">
              <div className="text-muted-foreground text-sm">
                {!isCollapsed ? (
                  <>
                    <p className="mb-2">No pages available</p>
                    <p className="text-xs">Contact your administrator for access</p>
                  </>
                ) : (
                  <p className="text-xs">No access</p>
                )}
              </div>
            </div>
          ) : (
            Object.entries(groupedItems).map(([category, items]) => (
              <div key={category} className="space-y-2">
                {/* Category separator for non-collapsed view */}
                {!isCollapsed && items.length > 0 && category !== 'main' && (
                  <div className="text-xs text-muted-foreground uppercase tracking-wider px-2 py-1">
                    {category}
                  </div>
                )}

                {items
                  .sort((a, b) => (a.sortOrder || 0) - (b.sortOrder || 0))
                  .map((item) => (
                    <DynamicSidebarItem
                      key={item.id}
                      item={item}
                      isCollapsed={isCollapsed && !isMobileView}
                      isActive={isRouteActive(item.href)}
                      onClick={isMobileView ? () => handleMobileNavigation(item.href) : undefined}
                    />
                  ))}
              </div>
            ))
          )}
        </nav>
      </ScrollArea>

      {/* Logout Button */}
      <div className="mt-auto px-4 pb-4">
        <DynamicSidebarItem
          item={{
            id: 'logout',
            icon: <LogOut color="white" />,
            label: 'Logout',
            href: '#',
            category: 'system',
            className: 'bg-primary hover:bg-primary/90 text-white w-full'
          }}
          isCollapsed={isCollapsed && !isMobileView}
          isActive={false}
          onClick={handleLogout}
        />
      </div>
    </div>

    {/* Logout Confirmation Modal */}
    <LogoutModal
      isOpen={showLogoutModal}
      onClose={handleLogoutCancel}
      onConfirm={handleLogoutConfirm}
    />
    </>
  );
};

export default DynamicSidebar;
